import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';
import Home from './components/Home';
import CardCreator from './components/CardCreator';
import { fetchSkills } from './services/SkillsService';
import './App.scss';
const defaultImg = 'data:image/jpeg;base64,';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      skills: [],
      preview: false,
      card: {
        name: 'Nombre Apellido',
        job: 'Front-end developer',
        photo: defaultImg,
        email: '',
        phone: '',
        linkedin: '',
        github: '',
        palette: '1',
        typography: '2',
        skills: []
      }
    }

    this.handleName = this.handleName.bind(this);
    this.handleJob = this.handleJob.bind(this);
    this.handleUrl = this.handleUrl.bind(this);
    this.handleEmail = this.handleEmail.bind(this);
    this.handlePhone = this.handlePhone.bind(this);
    this.handleLinkedin = this.handleLinkedin.bind(this);
    this.handleGithub = this.handleGithub.bind(this);
    this.handleColor = this.handleColor.bind(this);
    this.handleFonts = this.handleFonts.bind(this);
    this.handleSkills = this.handleSkills.bind(this);
  }

  fetchNewSkills() {
    fetchSkills()
      .then(data => {
        this.setState({
          skills: data.skills
        })
      })
  }

  handleName(e) {
    const cardState = this.state.card;
    this.setState(
      {
        card: { ...cardState, name: e.currentTarget.value }
      });
  }

  handleJob(e) {
    const cardState = this.state.card;
    this.setState(
      {
        card: { ...cardState, job: e.currentTarget.value }
      });
  }

  handleUrl(url) {
    const cardState = this.state.card;

    this.setState(
      {
        card: { ...cardState, photo: url },
        preview: true
      }
    )
  }

  handleEmail(e) {
    const cardState = this.state.card;
    this.setState(
      {
        card: { ...cardState, email: e.currentTarget.value }
      });
  }

  handlePhone(e) {
    const cardState = this.state.card;
    this.setState(
      {
        card: { ...cardState, phone: e.currentTarget.value }
      });
  }

  handleLinkedin(e) {
    const cardState = this.state.card;
    this.setState(
      {
        card: { ...cardState, linkedin: e.currentTarget.value }
      });
  }

  handleGithub(e) {
    const cardState = this.state.card;
    this.setState(
      {
        card: { ...cardState, github: e.currentTarget.value }
      });
  }

  handleColor(e) {
    const cardState = this.state.card;

    if (e.currentTarget.value === 'green') {

      this.setState(
        {
          card: { ...cardState, palette: '' }
        });

    } else if (e.currentTarget.value === 'red') {
      this.setState(
        {
          card: { ...cardState, palette: 'card__theme-red' }
        });

    } else if (e.currentTarget.value === 'blue') {
      this.setState(
        {
          card: { ...cardState, palette: 'card__theme-blue' }
        });
    }
  }

  handleFonts(e) {
    const cardState = this.state.card;

    if (e.currentTarget.id === 'comic') {

      this.setState(
        {
          card: { ...cardState, typography: '' }
        });

    } else if (e.currentTarget.id === 'ubuntu') {
      this.setState(
        {
          card: { ...cardState, typography: 'card__typo-ubuntu' }
        });

    } else if (e.currentTarget.id === 'montserrat') {
      this.setState(
        {
          card: { ...cardState, typography: 'card__typo-montserrat' }
        });
    }
  }

  handleSkills(e){
    const cardState = this.state.card;
    const currentSkills = cardState.skills.slice(0);
    const check = e.currentTarget;
    const newSkill = check.value;
    const isChecked = check.checked;

    if(currentSkills.length < 3 && isChecked) {
      currentSkills.push(newSkill);
    } else {
      check.checked = false;
      const index = currentSkills.indexOf(newSkill);
      if (index > -1) {
        currentSkills.splice(index, 1);
      }
    }

    const newCard = {...cardState, skills: currentSkills};

    this.setState({
      card: newCard
    });
  }

  componentDidMount(){
    this.fetchNewSkills();
  }

  render() {
    return (
      <React.Fragment>
        <Switch>
          <Route exact path='/' component={Home} />

          <Route path='/card' render={() => <CardCreator card={this.state.card} skills={this.state.skills} preview={this.state.preview} handleName={this.handleName} handleJob={this.handleJob} handleUrl={this.handleUrl} handleEmail={this.handleEmail} handlePhone={this.handlePhone} handleLinkedin={this.handleLinkedin} handleGithub={this.handleGithub} handleColor={this.handleColor} handleFonts={this.handleFonts} handleSkills={this.handleSkills}/>} />
        </Switch>
      </React.Fragment>
    )
  }
}

export default App;
